import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { DishesComponent } from './dishes.component';
import { RouterModule, Routes } from '@angular/router';
import { NgZorroAntModule } from 'src/app/cafe/desktop/ng-zorro-ant.module';
import { IonicModule } from '@ionic/angular';
import { TranslateModule } from '@ngx-translate/core';

const routes: Routes = [
  {
    path: '',
    component:  DishesComponent
  },
  {
    path: 'menu',
    loadChildren: () => import('./menu/menu.module').then(m => m.MenuModule)
  },
  {
    path: 'dish-list',
    loadChildren: () => import('./dish-list/dish-list.module').then(m => m.DishListModule)
  },
  {
    path: 'topping',
    loadChildren: () => import('./topping/topping.module').then(m => m.ToppingModule)
  },
  {
    path: 'combo',
    loadChildren: () => import('./combo/combo.module').then(m => m.ComboModule)
  },
  {
    path: 'combo-category',
    loadChildren: () => import('./combo-category/combo-category.module').then(m => m.ComboCategoryModule)
  }, 
  {
    path: 'topping-category',
    loadChildren: () => import('./topping-category/topping-category.module').then(m => m.ToppingCategoryModule)
  },
]

@NgModule({
  declarations: [DishesComponent],
  imports: [
    CommonModule,
    RouterModule.forChild(routes),
    NgZorroAntModule,
    IonicModule,
    TranslateModule
  ]
})
export class DishesModule { }
